'use client';

import { Watch, Users, Clock, Gamepad2, Trophy, Zap } from 'lucide-react';

interface ConceptProps {
  content: {
    title: string;
    description: string;
  };
}

export default function Concept({ content }: ConceptProps) {
  // Détecter la langue
  const isEnglish = !/[א-ת]/.test(content.description);
  
  // Textes selon la langue - basés sur active-games-main
  const featureTexts = isEnglish
    ? ['Up to 6 players', '60 minutes of action', 'Smart bracelet', '8 different rooms', 'Competitive or Team', 'Your strategy wins']
    : ['עד 6 שחקנים', '60 דקות אקשן', 'צמיד חכם', '8 חדרים שונים', 'תחרותי או צוותי', 'האסטרטגיה שלכם מנצחת'];

  const features = [
    { icon: Users, text: featureTexts[0] },
    { icon: Clock, text: featureTexts[1] },
    { icon: Watch, text: featureTexts[2] },
    { icon: Gamepad2, text: featureTexts[3] },
    { icon: Trophy, text: featureTexts[4] },
    { icon: Zap, text: featureTexts[5] },
  ];

  // Parser description en paragraphes
  const descriptionParagraphs = content.description.split('\n\n').filter(p => p.trim());

  return (
    <section id="concept" className="py-20 relative overflow-hidden" style={{ 
      background: 'linear-gradient(180deg, rgba(220,50,200,0.75) 0%, rgba(180,80,170,1) 100%)',
      borderRadius: '28px',
      boxShadow: '0px 0px 40px 0px rgba(0, 0, 0, 0.7)',
      marginTop: '0px',
      marginLeft: 'clamp(16px, 2vw, 40px)',
      marginRight: 'clamp(16px, 2vw, 40px)',
      paddingTop: 'clamp(40px, 6vw, 80px)',
      paddingBottom: 'clamp(40px, 6vw, 80px)',
      minHeight: '400px'
    }}>
      <div className="container mx-auto relative z-10" style={{ 
        paddingLeft: 'clamp(60px, 12vw, 160px)',
        paddingRight: 'clamp(60px, 12vw, 160px)'
      }}>
        <div className="max-w-3xl">
          {/* Section Header */}
          <div className="text-center mb-6">
            <h2 className="section-title" style={{ color: '#ffffff', textShadow: '0 0 15px rgba(255, 255, 255, 0.9), 0 0 40px rgba(8, 247, 254, 0.55), 0 0 60px rgba(255, 0, 229, 0.3)' }}>{content.title}</h2>
            <p className="leading-relaxed" style={{ fontFamily: 'Poppins, sans-serif', textShadow: '0 2px 4px rgba(0,0,0,0.3)', color: '#ffffff', fontSize: '1.25rem' }}>
              {isEnglish 
                ? 'Active Games is an innovative interactive gaming complex based on teamwork, movement, and strategic thinking.'
                : 'אקטיב גיימס הוא מתחם משחקים אינטראקטיבי חדשני המבוסס על עבודת צוות, תנועה וחשיבה אסטרטגית.'}
            </p>
          </div>

          {/* Features Grid - 2 lignes de 3 - Bulles futuristes avec espace */}
          <div className="mb-12">
            <div className="grid grid-cols-3" style={{ gap: '2rem' }}>
              {features.map((feature, index) => (
                <div
                  key={index}
                  className="backdrop-blur-sm text-center transition-all duration-300"
                  style={{
                    backgroundColor: 'rgba(26, 26, 46, 0.5)',
                    borderRadius: '16px',
                    border: '2px solid rgba(0, 240, 255, 0.3)',
                    boxShadow: '0 4px 20px rgba(0, 240, 255, 0.1), inset 0 1px 0 rgba(255, 255, 255, 0.1)',
                    padding: '1rem 0.875rem',
                    minWidth: '120px',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center',
                    alignItems: 'center'
                  }}
                  onMouseEnter={(e) => {
                    e.currentTarget.style.borderColor = 'rgba(0, 240, 255, 0.6)';
                    e.currentTarget.style.boxShadow = '0 8px 30px rgba(0, 240, 255, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.2)';
                    e.currentTarget.style.transform = 'translateY(-2px)';
                  }}
                  onMouseLeave={(e) => {
                    e.currentTarget.style.borderColor = 'rgba(0, 240, 255, 0.3)';
                    e.currentTarget.style.boxShadow = '0 4px 20px rgba(0, 240, 255, 0.1), inset 0 1px 0 rgba(255, 255, 255, 0.1)';
                    e.currentTarget.style.transform = 'translateY(0)';
                  }}
                >
                  <feature.icon className="w-8 h-8 mx-auto" style={{ color: '#08F7FE', filter: 'drop-shadow(0 0 8px rgba(8, 247, 254, 0.8))', marginBottom: '0.75rem' }} />
                  <p className="text-sm font-medium" style={{ fontFamily: 'Poppins, sans-serif', color: '#ffffff', margin: 0 }}>{feature.text}</p>
                </div>
              ))}
            </div>
          </div>

          {/* Description Paragraphs - Aligné à gauche */}
          <div className="space-y-6">
            {descriptionParagraphs.map((paragraph, index) => (
              <p key={index} className="leading-relaxed text-left" style={{ fontFamily: 'Poppins, sans-serif', textShadow: '0 2px 4px rgba(0,0,0,0.3)', color: '#ffffff', fontSize: '1.125rem' }}>
                {paragraph.trim()}
              </p>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
}
