'use client'

import { useState } from 'react'
import { AnimatePresence, motion } from 'framer-motion'
import Image from 'next/image'
import { X } from 'lucide-react'
import { gameAssets, gameNameToKey } from '@/lib/games'

interface GamesProps {
  content: {
    title: string;
    subtitle: string;
    items: Array<{
      name: string;
      description: string;
      video: string;
    }>;
  };
}

// Données par défaut pour popup_description et features
const gamePopupData: Record<string, { popup_description: string; features: string[] }> = {
  grid: {
    popup_description: 'The Active Games Grid immerses players in a captivating challenge of logic and reasoning on an interactive board. This dynamic game tests your analytical skills and strategy to complete the grid and achieve the objectives.',
    features: ['5 game modes', '50 levels to explore']
  },
  arena: {
    popup_description: 'An interactive LED hexagon target wall where players use dodgeballs to hit glowing targets. Each match combines memory, agility, and speed. Hit the right colors and avoid the red zones to maximize your score.',
    features: ['4 game modes', '40 levels to explore']
  },
  push: {
    popup_description: 'Push is an observation and strategy challenge game. Surrounded by multi-colored button walls, players need to quickly identify patterns and use strategy, memory, and collaboration to press the right buttons within the time limit.',
    features: ['5 game modes', '50 levels to explore']
  },
  basketball: {
    popup_description: 'A fully interactive fitness experience that combines the excitement of sports, the challenge of competition, and the innovation of modern technology. Every shot is recorded in real time.',
    features: ['LED-lit targets', 'Smart sensor tracking']
  },
  climbing: {
    popup_description: 'An interactive LED climbing wall game that simulates the thrill of cliff climbing. Players must step carefully along a narrow edge, grabbing green safe lights to move forward.',
    features: ['Grab green safe lights', 'Avoid dangerous red zones']
  },
  hide: {
    popup_description: 'A thrilling blend of stealth, strategy, and teamwork. Powered by advanced motion-sensing technology, the game challenges players to outsmart glowing « eyes » while completing interactive light sequences.',
    features: ['4 game modes', '40 levels to explore']
  },
  flash: {
    popup_description: 'Flash is a game of quick reaction challenges. Light tubes cover the walls and a flash of electricity falls from the ceiling – you need to react quickly to intercept each flash before it reaches the end.',
    features: ['4 game modes', '40 levels to explore']
  },
  laser: {
    popup_description: 'Creates a real-life action challenge where players must dodge, crawl, and weave through an array of laser beams. This exciting maze tests your reflexes, agility, and strategy.',
    features: ['Dynamic difficulty modes', 'For kids, teens, and adults']
  },
  control: {
    popup_description: 'Players stand in front of the console and control the movement of the light blocks by tapping the buttons on the console or stepping on the LED tiles.',
    features: ['Console control', 'LED tile interaction', 'Strategy gameplay']
  },
}

export default function Games({ content }: GamesProps) {
  const [selectedGame, setSelectedGame] = useState<string | null>(null)

  // Transformer content.items (array) en Record et créer gameKeys
  const gamesRecord: Record<string, {
    name: string
    description: string
    popup_description: string
    features: string[]
  }> = {}

  content.items.forEach((game) => {
    const key = gameNameToKey[game.name] || game.name.toLowerCase().replace(/\s+/g, '')
    gamesRecord[key] = {
      name: game.name,
      description: game.description,
      popup_description: gamePopupData[key]?.popup_description || game.description,
      features: gamePopupData[key]?.features || []
    }
  })

  // Filtrer pour exclure "control"
  const gameKeys = Object.keys(gamesRecord).filter(key => key !== 'control')

  const isEnglish = !/[א-ת]/.test(content.title)
  const moreInfoText = isEnglish ? 'More info' : 'מידע נוסף'

  return (
    <section id="games" className="py-20 relative" style={{ 
      background: 'linear-gradient(180deg, rgba(126,227,230,0.52) 0%, rgba(136,224,226,0.65) 100%)',
      borderRadius: '28px',
      boxShadow: '0px 0px 40px 0px rgba(0, 0, 0, 0.7)',
      marginTop: '0px',
      marginLeft: 'clamp(16px, 2vw, 40px)',
      marginRight: 'clamp(16px, 2vw, 40px)',
      paddingTop: '3rem',
      paddingBottom: '3.2rem',
      paddingLeft: '2.2rem',
      paddingRight: '2.2rem'
    }}>
      <div className="container mx-auto relative z-10" style={{ paddingLeft: 'clamp(60px, 12vw, 160px)', paddingRight: 'clamp(60px, 12vw, 160px)' }}>
        <div className="max-w-3xl">
          <div className="text-center mb-6">
            <h2 className="section-title" style={{ color: '#ffffff', textShadow: '0 0 15px rgba(255, 255, 255, 0.9), 0 0 40px rgba(8, 247, 254, 0.55), 0 0 60px rgba(255, 0, 229, 0.3)' }}>{content.title}</h2>
            <p className="leading-relaxed" style={{ fontFamily: 'Poppins, sans-serif', textShadow: '0 2px 4px rgba(0,0,0,0.3)', color: '#ffffff', fontSize: '1.25rem' }}>
              {content.subtitle}
            </p>
          </div>
          
          <div className="grid grid-cols-3" style={{ gap: '2rem' }}>
            {gameKeys.map((gameKey, index) => {
              const gameData = gamesRecord[gameKey]
              const assets = gameAssets[gameKey as keyof typeof gameAssets]
              const hasVideo = assets && assets.video !== null

              if (!gameData || !assets) return null

              return (
                <div
                  key={gameKey}
                  className="game-card-new group cursor-pointer bg-dark-100/90 backdrop-blur-sm rounded-2xl overflow-hidden border border-primary/30 hover:border-primary/50 transition-all duration-300 hover:shadow-[0_0_30px_rgba(0,240,255,0.2)]"
                  style={{ 
                    margin: '0',
                    borderRadius: '1rem'
                  }}
                  onClick={() => setSelectedGame(gameKey)}
                >
                  <div className="relative bg-black rounded-t-2xl overflow-hidden" style={{ height: '220px' }}>
                    {hasVideo ? (
                      <video
                        className="w-full h-full object-cover"
                        autoPlay
                        loop
                        muted
                        playsInline
                      >
                        <source src={assets.video} type="video/mp4" />
                        Your browser does not support the video tag.
                      </video>
                    ) : (
                      assets && 'thumb' in assets && (
                        <Image
                          src={assets.thumb}
                          alt={gameData.name}
                          fill
                          className="object-cover"
                          unoptimized
                        />
                      )
                    )}
                  </div>
                  <div style={{ paddingTop: '0.25rem', paddingBottom: '0.25rem', paddingLeft: '1.5rem', paddingRight: '1.5rem', backgroundColor: 'rgba(65, 70, 85, 0.92)' }}>
                    <h3 className="font-display text-2xl font-bold mb-3 transition-colors" style={{ color: '#ffffff' }}>
                      {gameData.name}
                    </h3>
                    <p className="text-base line-clamp-3 mb-4 leading-relaxed" style={{ fontFamily: 'Poppins, sans-serif', color: '#ffffff' }}>
                      {gameData.description}
                    </p>
                    <button 
                      className="text-base font-medium hover:underline flex items-center gap-2" 
                      style={{ fontFamily: 'Poppins, sans-serif', background: 'transparent', border: 'none', padding: 0, color: '#00f0ff' }}
                    >
                      {moreInfoText}
                      <span className="group-hover:translate-x-1 transition-transform">→</span>
                    </button>
                  </div>
                </div>
              )
            })}
          </div>
        </div>
      </div>

      {/* Game Modal */}
      <AnimatePresence>
        {selectedGame && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            className="modal-overlay"
            onClick={() => setSelectedGame(null)}
          >
            <motion.div
              initial={{ opacity: 0, scale: 0.9, y: 20 }}
              animate={{ opacity: 1, scale: 1, y: 0 }}
              exit={{ opacity: 0, scale: 0.9, y: 20 }}
              className="modal-content"
              onClick={(e) => e.stopPropagation()}
            >
              {/* Close Button */}
              <button
                onClick={() => setSelectedGame(null)}
                className="absolute top-4 right-4 z-10 w-10 h-10 rounded-full flex items-center justify-center transition-colors"
                style={{ backgroundColor: 'rgba(60, 60, 70, 0.9)', color: '#ffffff' }}
                onMouseEnter={(e) => { e.currentTarget.style.backgroundColor = 'rgba(70, 70, 80, 0.9)' }}
                onMouseLeave={(e) => { e.currentTarget.style.backgroundColor = 'rgba(60, 60, 70, 0.9)' }}
              >
                <X size={20} style={{ color: '#ffffff' }} />
              </button>

              {/* Modal Content */}
              {(() => {
                const gameData = gamesRecord[selectedGame]
                const assets = gameAssets[selectedGame as keyof typeof gameAssets]

                if (!gameData || !assets) return null

                const hasVideo = assets.video !== null

                return (
                  <>
                    {/* Vidéo ou Image */}
                    <div className="relative aspect-video bg-black">
                      {hasVideo ? (
                        <video
                          src={assets.video!}
                          autoPlay
                          loop
                          muted
                          playsInline
                          className="w-full h-full object-cover rounded-t-[20px]"
                        />
                      ) : (
                        assets && 'popup' in assets && (
                          <Image
                            src={assets.popup}
                            alt={gameData.name}
                            fill
                            className="object-cover rounded-t-[20px]"
                            unoptimized
                          />
                        )
                      )}
                    </div>

                    {/* Info */}
                    <div style={{ padding: '2rem' }}>
                      <h3 className="font-display text-3xl font-bold gradient-text mb-4">
                        {gameData.name}
                      </h3>
                      <p className="mb-6 leading-relaxed" style={{ fontFamily: 'Poppins, sans-serif', color: '#ffffff' }}>
                        {gameData.popup_description}
                      </p>

                      {/* Features */}
                      <ul className="space-y-3">
                        {gameData.features.map((feature, i) => (
                          <li key={i} className="flex items-center gap-3" style={{ fontFamily: 'Poppins, sans-serif', color: '#ffffff' }}>
                            <span style={{ width: '8px', height: '8px', backgroundColor: '#00f0ff', borderRadius: '50%', display: 'inline-block', flexShrink: 0, marginRight: '12px' }} />
                            {feature}
                          </li>
                        ))}
                      </ul>
                    </div>
                  </>
                )
              })()}
            </motion.div>
          </motion.div>
        )}
      </AnimatePresence>
    </section>
  );
}
